.overlay {
  position: fixed;
  overflow: scroll;
  white-space: nowrap;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: rgba(29, 29, 29, 0.767);
  color: rgb(20, 20, 20);
  z-index: 2;
  cursor: pointer;
}

.modal {
  margin: 1rem auto;
  padding: 2rem;
  width: 95%;
  max-width: 750px;
  border-radius: 2rem;
  text-align: center;
  background-color: rgb(255, 255, 255);
  z-index: 3;

  &__id {
    font-weight: 600;
    font-size: 1.25rem;
    color: white;
  }

  &__name {
    background-color: white;
    padding: 0.5rem;
    border-radius: 100rem;
    max-width: 420px;
    margin: auto;
  }

  img {
    margin: 1rem 0rem;
    padding: 1rem;
    border-radius: 100rem;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  }

  &__types {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1rem 0rem;

    &__text {
      margin: 0.5rem;
      padding: 0.5rem 1.25rem;
      border-radius: 1rem;
      color: white;
      background-color: #4b4b4b;
      font-weight: 500;
    }
  }

  &__items {
    &:not(:first-child) {
      margin-top: 2rem;
    }
  }
}

.basic_stats {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 1rem;
  padding: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

.height {
  margin: 0rem 1rem;
}

.basic_stats {
  background-color: white;
  border-radius: 100rem;
  max-width: 420px;
  margin: auto;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

.weight {
  margin: 0rem 1rem;
}

.base_stats {
  margin-top: 1.5rem;
  border-radius: 1rem;
  background-color: white;
  padding: 1rem;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  max-width: 420px;
  margin: 1.5rem auto;

  &__item {
    h3 {
      margin-top: 1rem;
      font-size: 1rem;
    }

    p {
      margin-top: 0.25rem;
      font-size: 0.9rem;
    }
  }
}

