.pokemon_card {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem;
  padding: 1rem 2rem;
  background-color: rgb(213, 213, 213);
  border-radius: 1rem;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px,
    rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
  min-width: 260px;
  min-height: 335px;
  transition: all 0.5s ease;
  cursor: pointer;

  &:hover {
    opacity: 0.85;
    transform: translateY(10px);
  }

  img {
    margin-top: 1.25rem;
    width: 110px;
    height: 110px;
  }

  &__id {
    font-weight: 600;
    margin-top: 1rem;
    text-align: center;
    color: white;
  }

  &__name {
    font-size: 1.25rem;
    color: white;
  }

  &__type {
    text-align: center;
    margin-top: 1rem;
    border-radius: 1rem;
    font-weight: 500;
    padding: 0.5rem 1rem;
    background-color: #4b4b4b;
    color: white;
  }
}

